<template>
  <div>
    <div>
      <strong>Product list</strong>
      <b-button
        variant="primary"
        class="mb-2 ml-3 actionButton"
        v-b-modal.productconfiguration
        pill
        ><i class="fas fa-plus-circle"></i
      ></b-button>
      <b-table
        style="font-size:80%"
        class="shadow-sm"
        responsive
        id="productsTable"
        :fields="$constants().productTableFields"
        :items="products"
        bordered
        hover
        small
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
          </div>
        </template>
        <template #cell(action)="row" align="center">
          <b-button-group class="col-md-5" align="center" size="sm">
            <b-button
              size="sm"
              variant="outline-info"
              class="actionButton"
              @click="row.toggleDetails"
              ><i class="fas fa-info"></i>
              {{ row.detailsShowing ? '' : '' }}
            </b-button>
            <b-button
              size="sm"
              class="actionButton ml-1"
              variant="outline-success"
              @click="editProduct(row)"
              ><i class="fas fa-edit"></i
            ></b-button>

            <b-button
              size="sm"
              class="actionButton ml-1"
              variant="outline-danger"
              @click="deleteLineProducts(row)"
              ><i class="fas fa-trash-alt"></i
            ></b-button>
          </b-button-group>
        </template>
        <template #row-details="row">
          <b-card>
            <vue-json-pretty
              :showDoubleQuotes="false"
              :showIcon="true"
              :data="row.item"
            />
          </b-card>
        </template>
      </b-table>
    </div>
    <b-modal
      id="productconfiguration"
      ref="productconfiguration"
      title="Product configuration"
      size="xl"
      hide-footer
    >
      <b-row class="mt-3">
        <b-col>
          <b-card>
            <b-row class="mb-3 mt-3">
              <b-row>
                <b-col>
                  <label for="serialNumber" class="form-label"
                    >Reference</label
                  >
                  <b-form-input
                    size="sm"
                    v-model="reference"
                    id="reference"
                  ></b-form-input>
                </b-col>
              </b-row>
              <b-row class="mt-2">
                <b-col>
                  <label for="description" class="form-label"
                    >Description</label
                  >
                  <b-form-input
                    size="sm"
                    v-model="description"
                    id="description"
                  ></b-form-input>
                </b-col>
              </b-row>

              <!-- CHECKLIST -------------------------------------------------------------------------------------->
              <b-row class="mt-5 mb-2">
                <b-col>
                  <label for="checkPointDescription" class="form-label"
                    >Check Point</label
                  >
                  <b-form-input
                    size="sm"
                    v-model="checkPointDescription"
                    id="checkPointDescription"
                  ></b-form-input>
                </b-col>
                <b-col cols="1" style="padding:32px 0px 0px 0px">
                  <b-button
                    variant="primary"
                    class="actionButton"
                    size="sm"
                    @click="addLineCheckPoints()"
                    >+</b-button
                  >
                </b-col>
                <b-col cols="6">
                  <b-table
                    style="font-size:80%"
                    id="checkListTable"
                    :fields="$constants().checkPointsTableFields"
                    :items="checkPoints"
                    small
                  >
                    <template #cell(action)="row" align="center">
                      <b-button-group align="center" size="sm">
                        <b-button
                          size="sm"
                          class="actionButton"
                          variant="outline-danger"
                          @click="deleteLineCheckPoints(row)"
                          ><i class="fas fa-trash-alt"></i
                        ></b-button>
                      </b-button-group>
                    </template>
                  </b-table>
                </b-col>
              </b-row>
              <div
                class="col-sm mt-5 mr-4 d-grid gap-2 d-md-flex justify-content-md-end"
              >
                <b-button
                  variant="primary"
                  class="actionButton"
                  @click="addLineProducts()"
                  >Add product</b-button
                >
                <b-button
                  variant="secondary"
                  class="actionButton"
                  @click="closeProductConfiguration()"
                  >Close</b-button
                >
              </div>
            </b-row>
          </b-card>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import VueJsonPretty from 'vue-json-pretty';
import 'vue-json-pretty/lib/styles.css';

export default {
  name: 'Product',
  data() {
    return {
      // Part fields
      id: '',
      reference: '',
      description: '',
      //--------------------
      products: [],
      checkPoints: [],
      checkPointDescription: ''
    };
  },
  methods: {
    addLineProducts() {
      if (this.id != '') {
        this.products.push({
          id: this.id,
          reference: this.reference,
          description: this.description,
          checkPoints: this.checkPoints
        });
      } else {
        this.products.push({
          reference: this.reference,
          description: this.description,
          checkPoints: this.checkPoints
        });
      }

      this.id = '';
      this.reference = '';
      this.description = '';
      this.checkPoints = [];
      this.checkPointDescription = '';
      this.heckPointDescription = '';
    },
    addLineCheckPoints() {
      this.checkPoints.push({
        description: this.checkPointDescription
      });

      this.checkPointDescription = '';
    },
    closeProductConfiguration() {
      this.$refs.productconfiguration.hide();
    },
    editProduct(row) {
      this.$refs.productconfiguration.show();
      if (row.item.id != undefined) {
        this.id = row.item.id;
      }
      this.reference = row.item.reference;
      this.description = row.item.description;
      this.checkPoints = row.item.checkPoints;

      this.products.splice(row.index, 1);
    },
    deleteLineProducts(row) {
      this.products.splice(row.index, 1);
    },
    deleteLineCheckPoints(row) {
      this.checkPoints.splice(row.index, 1);
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
  },
  components: {
    VueJsonPretty
  }
};
</script>
