<template>
  <div>
    <div>
      <strong>Part list</strong>
      <b-button
        variant="primary"
        class="mb-2 ml-3 actionButton"
        v-b-modal.partconfiguration
        pill
        @click="resetEditModal"
        ><i class="fas fa-plus-circle"></i
      ></b-button>
      <b-table
        style="font-size:80%"
        class="shadow-sm"
        responsive
        id="partsTable"
        :fields="$constants().partTableFields"
        :items="parts"
        bordered
        hover
        small
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
          </div>
        </template>
        <template #cell(action)="row" align="center">
          <b-button-group class="col-md-5" align="center" size="sm">
            <b-button
              size="sm"
              variant="outline-info"
              class="actionButton"
              @click="row.toggleDetails"
              ><i class="fas fa-info"></i>
              {{ row.detailsShowing ? '' : '' }}
            </b-button>
            <b-button
              size="sm"
              class="actionButton ml-1"
              variant="outline-success"
              @click="editPart(row)"
              ><i class="fas fa-edit"></i
            ></b-button>

            <b-button
              size="sm"
              class="actionButton ml-1"
              variant="outline-danger"
              @click="deleteLinePart(row)"
              ><i class="fas fa-trash-alt"></i
            ></b-button>
          </b-button-group>
        </template>
        <template #row-details="row">
          <b-card>
            <vue-json-pretty
              :showDoubleQuotes="false"
              :showIcon="true"
              :data="row.item"
            />
          </b-card>
        </template>
      </b-table>
    </div>
    <b-modal
      id="partconfiguration"
      ref="partconfiguration"
      title="Part configuration"
      size="xl"
      hide-footer
      @close="hiddenModal"
    >
      <b-row class="mt-3">
        <b-col>
          <b-card>
            <b-row class="mb-3 mt-3">
              <b-row>
                <b-col cols="2">
                  <label for="partNumber" class="form-label">Number</label>
                  <b-form-input
                    size="sm"
                    v-model="partNumber"
                    id="partNumber"
                  ></b-form-input>
                </b-col>
                <b-col cols="2">
                  <label for="type" class="form-label">Type</label>
                  <b-form-select
                    size="sm"
                    v-model="type"
                    :options="$constants().optionsPartType"
                  >
                  </b-form-select>
                  <b-form-checkbox
                    v-if="type == 'Single'"
                    id="checkList"
                    v-model="checkList"
                    name="checkList"
                    value="true"
                    size="sm"
                    unchecked-value="false"
                    class="mt-2 mb-2"
                  >
                    Checklist
                  </b-form-checkbox>
                </b-col>

                <b-col>
                  <label for="partDescription" class="form-label"
                    >Description</label
                  >
                  <b-form-input
                    size="sm"
                    v-model="partDescription"
                    id="partDescription"
                  ></b-form-input>
                </b-col>
              </b-row>
              <!-- DEFECT -------------------------------------------------------------------------------------->
              <b-row class="mt-5 mb-2">
                <b-col>
                  <label for="defectDescription" class="form-label"
                    >Defect</label
                  >
                  <b-form-input
                    size="sm"
                    v-model="defectDescription"
                    id="defectDescription"
                  ></b-form-input>
                </b-col>
                <b-col>
                  <label for="defectPhoto" class="form-label">Photo</label>
                  <b-form-select
                    size="sm"
                    v-model="defectPhoto"
                    :options="$constants().optionsPhoto"
                  >
                  </b-form-select>
                </b-col>
                <b-col cols="1" style="padding:32px 0px 0px 0px">
                  <b-button
                    variant="primary"
                    :disabled="defectDescription == ''"
                    class="actionButton"
                    size="sm"
                    @click="addLineDefect()"
                    >+</b-button
                  >
                </b-col>
                <b-col cols="6">
                  <strong>Defects</strong>
                  <b-table
                    style="font-size:80%"
                    id="parts"
                    :fields="$constants().defectTableFields"
                    :items="defects"
                    small
                    class="mt-2"
                  >
                    <template #cell(action)="row" align="center">
                      <b-button-group align="center" size="sm">
                        <b-button
                          size="sm"
                          class="actionButton"
                          variant="outline-danger"
                          @click="deleteLineDefect(row)"
                          ><i class="fas fa-trash-alt"></i
                        ></b-button>
                      </b-button-group>
                    </template>
                    <template v-slot:cell(description)="data">
                      <b-input
                        v-model="defects[data.index].description"
                        size="sm"
                      />
                    </template>
                    <template v-slot:cell(photo)="data">
                      <b-form-select
                        size="sm"
                        v-model="defects[data.index].photo"
                        :options="$constants().optionsPhoto"
                      >
                     </b-form-select>
                    </template>
                  </b-table>
                </b-col>
              </b-row>

              <!-- REWORK -------------------------------------------------------------------------------------->
              <b-row class="mt-3 mb-2" v-if="type == 'Bulk'">
                <b-col>
                  <label for="reworkDescription" class="form-label"
                    >Rework</label
                  >
                  <b-form-input
                    size="sm"
                    v-model="reworkDescription"
                    id="reworkDescription"
                  ></b-form-input>
                </b-col>
                <b-col>
                  <label for="reworkPhoto" class="form-label">Photo</label>
                  <b-form-select
                    size="sm"
                    v-model="reworkPhoto"
                    :options="$constants().optionsPhoto"
                  >
                  </b-form-select>
                </b-col>
                <b-col cols="1" style="padding:32px 0px 0px 0px">
                  <b-button
                    variant="primary"
                    :disabled="reworkDescription == ''"
                    class="actionButton"
                    size="sm"
                    @click="addLineRework()"
                    >+</b-button
                  >
                </b-col>

                <b-col cols="6">
                  <strong>Reworks</strong>
                  <b-table
                    style="font-size:80%"
                    id="parts"
                    :fields="$constants().reworkTableFields"
                    :items="reworks"
                    small
                    class="mt-2"
                  >
                    <template #cell(action)="row" align="center">
                      <b-button-group align="center" size="sm">
                        <b-button
                          size="sm"
                          class="actionButton"
                          variant="outline-danger"
                          @click="deleteLineRework(row)"
                          ><i class="fas fa-trash-alt"></i
                        ></b-button>
                      </b-button-group>
                    </template>
                    <template v-slot:cell(description)="data">
                      <b-input
                        v-model="reworks[data.index].description"
                        size="sm"
                      />
                    </template>
                    <template v-slot:cell(photo)="data">
                      <b-form-select
                        size="sm"
                        v-model="reworks[data.index].photo"
                        :options="$constants().optionsPhoto"
                      >
                     </b-form-select>
                    </template>
                  </b-table>
                </b-col>
              </b-row>
              <!------------------------------------------------------------------------------------------------>

              <div
                class="col-sm mt-5 mr-4 d-grid gap-2 d-md-flex justify-content-md-end"
              >
                <b-button
                  variant="primary"
                  :disabled="partNumber == '' || type == ''"
                  class="actionButton"
                  @click="addLinePart()"
                  >Save</b-button
                >
                <b-button
                  variant="secondary"
                  class="actionButton"
                  @click="closePartConfiguration()"
                  >Close</b-button
                >
              </div>
            </b-row>
          </b-card>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import VueJsonPretty from 'vue-json-pretty';
import 'vue-json-pretty/lib/styles.css';

export default {
  name: 'Part',
  data() {
    return {
      // Part fields
      id: '',
      partNumber: '',
      partDescription: '',
      type: '',
      //--------------------
      parts: [],
      defects: [],
      reworks: [],
      defectDescription: '',
      reworkDescription: '',
      defectPhoto: 'Photo optional',
      reworkPhoto: 'Photo optional',
      edit: false,
      rowIndex: ''
    };
  },
  methods: {
    addLinePart() {

      if(this.edit == true) {this.parts.splice(this.rowIndex, 1);}
          
      if (this.id != '') {
        this.parts.push({
          id: this.id,
          number: this.partNumber,
          description: this.partDescription,
          type: this.type,
          checkList: this.checkList,
          defects: this.defects,
          reworks: this.reworks
        });
      } else {
        this.parts.push({
          number: this.partNumber,
          description: this.partDescription,
          type: this.type,
          checkList: this.checkList,
          defects: this.defects,
          reworks: this.reworks
        });
      }

      this.id = '';
      this.partNumber = '';
      this.partDescription = '';
      this.type = '';
      (this.checkList = false), (this.defects = []);
      this.reworks = [];
      this.defectDescription = '';
      this.defectPhoto = 'Photo optional';
      this.reworkDescription = '';
      this.reworkPhoto = 'Photo optional';

      this.resetEditModal();
    },
    addLineDefect() {
      this.defects.push({
        description: this.defectDescription,
        photo: this.defectPhoto
      });

      this.defectDescription = '';
      this.defectPhoto = 'Photo optional';
    },
    addLineRework() {
      this.reworks.push({
        description: this.reworkDescription,
        photo: this.reworkPhoto
      });

      this.reworkDescription = '';
      this.reworkPhoto = 'Photo optional';
    },
    closePartConfiguration() {
      this.$refs.partconfiguration.hide();
      this.resetEditModal();
      
    },
    editPart(row) {
      this.$refs.partconfiguration.show();
      if (row.item.id != undefined) {
        this.id = row.item.id;
      }
      this.partNumber = row.item.number;
      this.partDescription = row.item.description;
      this.type = row.item.type;
      this.checkList = row.item.checkList;
      this.defects = row.item.defects;
      this.reworks = row.item.reworks;

      this.edit = true;
      this.rowIndex = row.index;
    },
    deleteLinePart(row) {
      this.parts.splice(row.index, 1);
    },
    deleteLineDefect(row) {
      this.defects.splice(row.index, 1);
    },
    deleteLineRework(row) {
      this.reworks.splice(row.index, 1);
    },
    resetEditModal()
    {
      this.edit = false;
      this.rowIndex = '';
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
  },
  components: {
    VueJsonPretty
  }
};
</script>
