<template>
  <div>
    <b-collapse id="formCollapse">
      <b-form @submit.prevent="processForm">
        <h4 class="mt-4 mb-3">Job information</h4>
        <b-row class="mb-3">
          <b-col>
            <label for="jobNumber" class="form-label">Job number</label>
            <b-form-input v-model="jobNumber" id="jobNumber"></b-form-input>
            <p style="font-size: 70%;color: red;">Required</p>
          </b-col>
          <b-col>
            <label for="externalId" class="form-label">External Id</label>
            <b-form-input v-model="externalId" id="externalId"></b-form-input>
          </b-col>
          <b-col>
            <label for="site" class="form-label">Site</label>
            <div class="input-group">
              <b-form-select v-model="site">
                <option v-for="sit in sites" :value="sit" :key="sit.id">{{
                  sit.name
                }}</option>
              </b-form-select>
             
            </div>
            <p style="font-size: 70%;color: red;">Required</p>
          </b-col>
          <b-col>
            <label for="customer" class="form-label">Customer</label>
            <div class="input-group">
              <b-form-select v-model="customer">
                <option v-for="cus in customers" :value="cus" :key="cus.id">{{
                  cus.name
                }}</option>
              </b-form-select>
              
            </div>
            <p style="font-size: 70%;color: red;">Required</p>
          </b-col>
          <b-col>
            <label for="supplierName" class="form-label">Supplier name</label>
            <b-form-input
              v-model="supplierName"
              id="supplierName"
            ></b-form-input>
            <p style="font-size: 70%;color: red;">Required</p>
          </b-col>
        </b-row>
        <b-row class="mb-5">
          <b-col>
            <label for="comment" class="form-label">Comment</label>
            <b-form-input v-model="comment" id="comment"></b-form-input>
          </b-col>
          <b-col cols="3">
            <label for="type" class="form-label">Type</label>
            <b-form-select
              :disabled="this.id != ''"
              v-model="type"
              :options="$constants().optionsJobType"
            >
            </b-form-select>
            <p style="font-size: 70%;color: red;">Required</p>
          </b-col>
        </b-row>
        <Part v-show="type == 'Part'" ref="part"></Part>
        <Product v-show="type == 'Product'" ref="product"></Product>
        <div class="d-grid gap-2 mt-5 d-md-flex justify-content-md-end">
          <b-button
            variant="outline-danger"
            class="actionButton"
            v-b-toggle:formCollapse
            @click="reset()"
            ><i class="far fa-times-circle mr-1"></i>Cancel</b-button
          >
          <b-button
            :disabled="jobNumber == '' || site == '' || customer == '' || supplierName == ''"
            variant="outline-success"
            type="submit"
            class="actionButton"
            v-b-toggle:formCollapse
            ><i class="far fa-check-circle mr-1"></i
            >{{ txtBtnFormulario }}</b-button
          >
        </div>
      </b-form>
    </b-collapse>
  </div>
</template>

<script>
import Part from './PartComponent.vue';
import Product from './ProductComponent.vue';

export default {
  name: 'Job',
  data() {
    return {
      // Job fields
      id: '',
      jobNumber: '',
      externalId: '',
      supplierName: '',
      comment: '',
      customer: '',
      site: '',
      status: '',
      type: '',
      //--------------------
      perPage: 10,
      currentPage: 1,
      txtBtnFormulario: 'Save',
      editState: false,
      customers: [],
      sites: [],
      infoModalCustomer: {
        id: 'info-modal-customer',
        title: '',
        content: ''
      },
      infoModalSite: {
        id: 'info-modal-site',
        title: '',
        content: ''
      }
    };
  },
  methods: {
    getData() {
      this.$parent
        .getDataFromType('customer')
        .then(data => {
          this.customers = data;
        })
        .catch(error => this.$parent.catchError(error));
      this.$parent
        .getDataFromType('site')
        .then(data => {
          this.sites = data;
        })
        .catch(error => this.$parent.catchError(error));
    },
    processForm() {
      if (this.editState == false) {
        var dataSave = {
          jobNumber: this.jobNumber,
          type: this.type,
          externalId: this.externalId,
          supplierName: this.supplierName,
          comment: this.comment,
          customer: this.customer,
          site: this.site,
          status: 'CREATED',
          parts: this.$refs.part.parts,
          products: this.$refs.product.products
        };

        this.$parent.saveData(dataSave);
      } else {
        var dataUpdate = {
          id: this.id,
          jobNumber: this.jobNumber,
          type: this.type,
          externalId: this.externalId,
          supplierName: this.supplierName,
          comment: this.comment,
          customer: this.customer,
          site: this.site,
          status: 'UPDATED',
          parts: this.$refs.part.parts,
          products: this.$refs.product.products
        };

        this.$parent.updateData(dataUpdate);
      }

      this.reset();
    },
    closeJob(item)
    {
      var dataUpdate = {
        id: item.id,
        jobNumber: item.jobNumber,
        type: item.type,
        externalId: item.externalId,
        supplierName: item.supplierName,
        comment: item.comment,
        customer: item.customer,
        site: item.site,
        status: 'CLOSED',
        parts: item.parts,
        products: item.products
      };

      this.$parent.updateData(dataUpdate);

      this.reset();
    },
    reset() {
      this.id = '';
      this.jobNumber = '';
      this.externalId = '';
      this.supplierName = '';
      this.comment = '';
      this.customer = '';
      this.site = '';
      this.status = '';
      (this.type = ''), (this.$refs.part.parts = []);
      this.$refs.product.products = [];
      this.txtBtnFormulario = 'Save';
      this.editState = false;
      this.jobType = '';
    },
    loadItem(item) {
      this.id = item.id;
      this.jobNumber = item.jobNumber;
      this.type = item.type;
      this.externalId = item.externalId;
      this.supplierName = item.supplierName;
      this.comment = item.comment;
      this.customer = item.customer;
      this.site = item.site;
      this.status = item.status;
      this.$refs.part.parts = item.parts;
      this.$refs.product.products = item.products;
      this.txtBtnFormulario = 'Update';
      this.editState = true;
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
    this.getData();
  },
  components: {
    Part,
    Product
  }
};
</script>
